
.page {
	margin: 0;
	margin-top: 30px;
}
/*content - strenghts and weakness */
.team {
	background-color: #ffffff;
	margin-top: 80px;
}
.team h2{
	height: 45px;
	font-family: 'Rubik', sans-serif;
	font-size: 24px;
	font-weight: 500;
	line-height: 1.88;
	margin: auto;
	color: #14d9c4;
}

.blocks  {
	display: flex;
	flex-direction: column;
	font-size: 16px;
	background-color: rgba(0, 0, 0, 0.08);
}
.blocks h3 {
	font-family: 'Rubik', sans-serif;
	font-size: 22px;
	font-weight: 500;
}
.blocks-strenghts {
	margin-top: 30px;
	margin-bottom: 30px;
}
.blocks-weakness {
	margin-bottom: 45px;
}
.spike {
	background-color: #ffffff;
	width: 30px;
	height: 30px;
	transform: rotate(45deg) translate(13px,9px);
	margin: auto;
}
h3 {
	display: flex;
	justify-content: center;
	font-style: 'Rubik';
	font-size: 22px;
	color: #099d8d;
}